html {
  overflow-y: auto;
}

#page-body {
  max-width:1000px;
  margin: auto;
  padding-top: 72px;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  overflow-y: auto;
}

h3, h4 {
  margin-bottom: 0;
}

nav {
  border-bottom: 4px solid black;
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  z-index: 1;
}

nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  display: inline-block;
  padding-top: 24px;
  padding-bottom: 24px;
}

nav a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  padding: 24px 32px;
}

nav a:hover {
  background: black;
  color: white;
}

.reflect-list-item {
  font-size: 24px;
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.reflect-list-item-p {
  font-size: 18px;
  text-decoration: none;
  color: black;
  padding-bottom: 32px;
}

.nav-right {
  position: absolute;
  top: 8px;
  right: 8px;
}


/* TIMELINE
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.timeline {
  margin: 0 auto;
  max-width: 1000px;
  color: #9033ff;
}

.timeline ol {
  position: relative;
  list-style: none;
}

.timeline ol::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 100%;
  background: #69f6da;
}

.timeline ol li .time-wrapper {
  padding-top: 20px;
  position: absolute;
  left: 20px;
  font-size: 4.5rem;
  font-weight: bold;
  line-height: 0.9;
  z-index: 1;
}

.timeline ol li .time-wrapper::before,
.timeline ol li .time-wrapper::after {
  content: "";
  position: absolute;
  background: #69f6da;
}

.timeline ol li .time-wrapper::before {
  top: 50%;
  left: -180px;
  transform: translateY(-50%);
  width: 0;
  height: 3px;
  transition: width 0.8s linear;
}

.timeline ol li .time-wrapper::after {
  top: calc(50% - 8px);
  left: -60px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  transform: scale(0);
}

.tl-year {
  padding: 20px 180px;
  border-color: red;
  border-style: solid;
  border-width: 2px;
  margin: 10px;
}

.tl-year h3 {
  font-size: 2rem;
  line-height: 1;
  margin: 15px 0;
}

.tl-semester {
  display: flex;
  margin-top: 5px;
  position: relative;
  left: -150px;
  width: 700px;
  border-color: blue;
  border-style: dotted;
  border-width: 3px;
  background-color: blanchedalmond;
  z-index: 0;
}

.tl-semester-label {
  background-color: cornflowerblue;
  padding-top: 50px;
  margin-right: 30px;
  writing-mode:vertical-lr;
  text-orientation: upright;
}

.tl-semester-classes {
  background-color: aquamarine;
  flex-direction: column;
}

.tl-class {
  margin-top: 10px;
  padding: 10px;
  width: 600px;
  background-color: gainsboro;
  border-style: solid;
  border-color: black;
  border-width: 1px;
  border-radius: 15px 50px;
}